/**
 * Helper to convert a node list to an array.
 * Needed beacuse IE is a steaming pile of ****.
 * TODO: Drop IE support.
 * @param {!NodeList} list
 */
function toArray(list) {
  const array = [];
  for (let i = 0, e; e = list[i]; i++) {
    array.push(e);
  }
  return array;
}

export {toArray};



